@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;600&display=swap);
.Memory{background:#e6f0ff;padding:20px;overflow:visible;transition:all 100ms}.Memory__row,.Memory__column{fill:transparent}.Memory__row_selected,.Memory__column_selected{stroke:#488dff;stroke-width:2px}.Memory__header{fill:#515e6b;text-anchor:end;dominant-baseline:middle}.Memory__cell{fill:#bbbbbb;transition:0.35s}.Memory__cell_dirty{fill:#8fbaff}.Memory__cell_selected{fill:#488dff}.Memory__cellAddress{fill:#bbbbbb;transition:0.35s}.Memory__cellAddress_dirty{fill:#8fbaff}.Memory__cellAddress_selected{fill:#488dff}.Memory__addressLabel{font-size:0.875rem}.Memory__addressLabel,.Memory__dataLabel{font-family:"Roboto Mono", monospace;fill:white;text-anchor:end;dominant-baseline:middle}

.RamVisualization{padding:2rem 6.25rem}.RamVisualization__caption{display:flex}.RamVisualization__header{color:#515e6b;margin-bottom:2.25rem;margin-right:1rem}.RamVisualization__visualization{display:flex}.RamVisualization__visualizationSettings{min-width:15rem}.RamVisualization__memory{display:flex}.RamVisualization__notes{margin-top:1.25rem;font-size:0.75rem;color:#515e6b;opacity:0.7}

@-webkit-keyframes emphasise{0%{font-weight:600}100%{font-weight:400}}@keyframes emphasise{0%{font-weight:600}100%{font-weight:400}}@-webkit-keyframes hideshow{0%{stroke-dashoffset:-100}100%{stroke-dashoffset:-30}}@keyframes hideshow{0%{stroke-dashoffset:-100}100%{stroke-dashoffset:-30}}.PinsBlock{padding:8px 0 0 0;display:flex;height:-webkit-max-content;height:-moz-max-content;height:max-content}.PinsBlock__length{padding-right:16px;width:190px}.PinsBlock__list{margin-left:auto}.PinsBlock__pinArea{cursor:pointer;fill:transparent}.PinsBlock__pinLabel{fill:#515e6b;transform:translateX(30px)}.PinsBlock__pinLabel_active{-webkit-animation:emphasise 0.15s forwards;animation:emphasise 0.15s forwards;animation-direction:reverse}.PinsBlock__pinSignificance{fill:#515e6b;opacity:0.7;font-size:0.675rem}.PinsBlock__pinsNumber{fill:#515e6b;font-weight:600;opacity:0.4}.PinsBlock__pinsNumber_visible{transition-delay:1s}.PinsBlock__pinsNumber_invisible{opacity:0}.PinsBlock__pinsDivider{stroke:#515e6b;transform:translateX(30px)}.PinsBlock__pinsDivider_visible{transition-delay:1s}.PinsBlock__pinsDivider_invisible{opacity:0}.PinsBlock__pin,.PinsBlock__crossNotation,.PinsBlock__pinsNotation{stroke:#515e6b}.PinsBlock__pin{stroke-dashoffset:-30}.PinsBlock__pin_long{stroke-dasharray:100 100;transition:1s ease-in}.PinsBlock__pin_short{stroke-dasharray:25 100;transition:1s ease-in}.PinsBlock__crossNotation{opacity:0;stroke:#515e6b;stroke-width:2px;stroke-linecap:round}.PinsBlock__crossNotation_visible{opacity:0.5;transform-origin:center;transform-box:fill-box;transform:rotate(-45deg);transition:transform 1s, opacity 0s;transition-delay:1s}.PinsBlock__pinsNotation{stroke-width:3;opacity:0.5;stroke-dashoffset:-30;stroke-dasharray:100}.PinsBlock__pinsNotation_visible{stroke-dashoffset:-30;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation:hideshow 1s ease-in 1;animation:hideshow 1s ease-in 1}

.MemoryControls__tacts{display:block;margin:1rem 0}.MemoryControls__tactsNumber{width:2rem;margin-right:0.5rem}.MemoryControls__bitsWidth{display:block;color:#515e6b;font-weight:300;opacity:0.8}.MemoryControls__clockPinBlock{height:120px;opacity:1;transition:height 700ms, opacity 700ms}.MemoryControls__clockPinBlock_hidden{height:0;opacity:0;transition:height 700ms, opacity 500ms}.MemoryControls__enabledBlock,.MemoryControls__readWriteBlock,.MemoryControls__clockPinBlock,.MemoryControls__addressBlock,.MemoryControls__dataBlock{display:flex;justify-content:space-between;background:#cfcfcf24;padding:0.25rem 0 0.25rem 3rem}.MemoryControls__enabledBlock,.MemoryControls__readWriteBlock,.MemoryControls__clockPinBlock,.MemoryControls__addressBlock{margin-top:0.5rem}.MemoryControls__enabledLabel,.MemoryControls__readWriteLabel,.MemoryControls__clockLabel,.MemoryControls__addressLabel,.MemoryControls__dataLabel{width:10rem;margin-right:1.5rem;padding:9px 0 8px 0;line-height:20px;color:#515e6b}.MemoryControls__enabledLabel,.MemoryControls__read,.MemoryControls__write{cursor:pointer}.MemoryControls__enabledLabel_enabled,.MemoryControls__read_enabled,.MemoryControls__write_enabled{font-weight:bold}.MemoryControls__dataMode{display:flex}.MemoryControls__currentTacts{opacity:0.7;font-size:0.875rem}.MemoryControls__tactsNote{opacity:0.7;font-size:0.75rem;white-space:nowrap}.MemoryControls__ras,.MemoryControls__cas{position:relative}.MemoryControls__rasLabel,.MemoryControls__casLabel{position:absolute;top:12px;left:20px;margin-right:0.5rem;margin-bottom:10px;font-size:0.75rem;color:#515e6b;text-transform:uppercase}.MemoryControls__rasLabel_active,.MemoryControls__casLabel_active{border-top:1px solid #515e6b;font-weight:bold}.MemoryControls__addressPins{width:160px}

.VisualizationSettings{display:flex}.VisualizationSettings__pinNotationInput,.VisualizationSettings__tactingInput,.VisualizationSettings__rasCasInput{height:1.5rem;width:1.5rem;margin-left:1.5rem;margin-right:0.125rem}.VisualizationSettings__pinNotation,.VisualizationSettings__tacting,.VisualizationSettings__rasCas{color:#515e6b;margin-bottom:0.25rem;display:flex;align-items:center}

input[type="range"]{-webkit-appearance:none;margin:18px 0;width:100%}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-runnable-track{background:#505e6b5e;height:2px;border-radius:2px}input[type="range"]::-moz-range-track{width:100%;height:2px;cursor:pointer;background:#505e6b5e;border-radius:2px;border:none}input[type="range"]::-webkit-slider-thumb{box-shadow:1px 1px 1px #000000, 0px 0px 1px #0d0d0d;border:1px solid #515e6b;height:14px;width:14px;border-radius:100%;-webkit-appearance:none;background:#ffffff;margin-top:-5px;cursor:pointer}input[type="range"]::-moz-range-thumb{box-shadow:1px 1px 1px #515e6b, 0px 0px 1px #0d0d0d;border:1px solid #515e6b;height:12px;width:12px;border-radius:100%;background:#ffffff;cursor:pointer}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

